@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+KR:wght@200;300;400;500;600;700;900&display=swap');

.serif{
  font-family: 'Noto Serif KR', serif;
}
.sans{
  font-family: 'Noto Sans KR', sans-serif;
}
.container{
  margin: auto;
  max-width: 600px;
}
.todoBlock{
  padding: 30px;
  margin-top: 50px;
  background: #000;
  border-radius: 10px;
  box-shadow: -9px 17px 13px rgb(0 0 0 /16%);
  margin-bottom: 50px;
}
a{
  text-decoration: none; 
  color:inherit;
}
/* App 9-3 Lists 파일 작성할때 추가 */
.title {font-size: 12px; color: #ffd500;}
.list {background-color: #000; border: 1px solid #000; border-radius: 5px; padding: 13px; display: flex; justify-content: space-between; margin: 5px 0;color:#ffd500; }
.list span {flex:1; margin-left: 5px; font-family: 'Noto Sans KR', sans-serif; }
.btn {background: none; border: none; color:#ffd500;}
form {display: flex; justify-content: space-between; margin-top: 10px;}
form input[type=text] {background: #000; border: 1px solid #ffd500;border-radius: 5px; box-shadow: 0 1px 1px rgb(0 0 0 /5%); height: 45px; font-size: 15px; padding: 0 10px; width: 86.5%; box-sizing: border-box; outline: none;
color:#ffd500;}
form input[type=text]::placeholder{color:#ffd500;}
form input[type=submit] {width: 12%;height: 45px;background: #ffd500; color: #000; border-radius: 5px; border: none;font-size: 15px;
cursor:pointer;}
input[type=checkbox] {opacity: 0;position: absolute;z-index: 12;width: 20px;height: 20px;}
input[type=checkbox] + span {position: relative;padding-left: 30px;}
input[type=checkbox] + span:before {content: '';position: absolute;left: 0; top: 50%; transform: translateY(-50%); width: 20px; height: 20px; line-height: 20px;border-radius: 3px; background: #fff; border: 1px solid #e6e6e6;}
input[type=checkbox]:checked + span:before { color: #fff;content: '✔'; color: #ffd500; text-align: center; font-size: 20px;}
/* 체크표시 보통 이미지나 웹폰트 많이 쓰지만 그냥 기호 복사해서 썼음 -> https://unicode-table.com/kr/search/?q=checkbox */

/* 할일 리스트 completed true 일때 span.completed */
.completed {text-decoration: line-through; color: #999;}

/* App 9-3 dragging 할 때 스타일링 */
.dragging .list {background-color: #ffd500; color:#000;}

/* App 10. 모두 삭제 버튼 추가 */
.title{display: flex; justify-content: space-between;}
.deleteBtn{background: none; font-weight: bold; color:#ffd500; 
border:none; font-size: 16px; cursor:pointer;}

/* git 바로가기 */
.gitBtn{
  text-align: center;
}
.gitBtn a{
  background: #000;
  color: #ffd500;
  padding: 10px 25px;
  border-radius: 45px;
  font-size: 1rem;
}
.gitBtn a:hover, .gitBtn a:focus{
  transition: all 0.5s ease;
  box-shadow:3px 3px 3px rgba(0,0,0,0.3);
}